.note-display {
  background: white;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
}

.hint-text {
  text-align: center;
  margin-top: 20px;
  font-size: 1.2rem;
  color: #FF69B4;
  font-weight: bold;
}

.vexflow-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}